<template>
	<div id="member-mine">
		<public-header title="我的"></public-header>
		<public-footer :menufooter="menufooter" :showPreLoading="showPreLoading"></public-footer>
		<div class="content">
			<router-link tag="div" :to="util.getUrl({path: 'pages/member/profile'})" class="padding-15-lr padding-10-tb margin-10-t bg-default flex-lr">
				<div class="userinfo flex">
					<img class="avatar margin-15-r" src="static/img/zfb-icon.png" alt="">
					<div>
						<div>{{plateformer.username}}</div>
						<div class="c-gray font-14 margin-10-t">{{plateformer.role_type}}</div>
					</div>
				</div>
				<van-icon name="arrow"></van-icon>
			</router-link>
			<template v-if="util.getUser().role == 'agenter'">
				<van-cell-group class="margin-10-t">
					<van-cell is-link :to="util.getUrl({path: 'pages/plugin/agent/config/index'})">
						<div slot="title" class="flex">
							<i class="icon icon-settings margin-5-r font-18 c-primary"></i>代理设置
						</div>
					</van-cell>
				</van-cell-group>
				<van-cell-group class="margin-10-t">
					<van-cell is-link :to="util.getUrl({path: 'pages/plugin/agent/finance/getcash'})">
						<div slot="title" class="flex">
							<i class="icon icon-settings margin-5-r font-18 c-primary"></i>申请提现
						</div>
					</van-cell>
				</van-cell-group>
			</template>
			<template v-else>
				<van-cell-group class="margin-10-t">
					<van-cell is-link :to="util.getUrl({path: 'pages/config/index'})">
						<div slot="title" class="flex">
							<i class="icon icon-settings margin-5-r font-18 c-primary"></i>平台设置
						</div>
					</van-cell>
				</van-cell-group>
			</template>
			<van-cell-group class="margin-10-t">
				<van-cell is-link :to="util.getUrl({path: 'pages/member/phonic'})">
					<div slot="title" class="flex">
						<i class="icon icon-settings margin-5-r font-18 c-primary"></i>语音设置
					</div>
				</van-cell>
			</van-cell-group>
		</div>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import publicFooter from "@/components/footer"
	import {getData} from "@/controller/funcCommon.js"
	export default {
		components: {
			publicHeader,
			publicFooter,
		},
		data() {
			return {
				plateformer: {
					username: '',
					role_type: '',
				},
				showPreLoading: true,
				menufooter: {}
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'plateform/member/mine',
					data: {
						menufooter: 1
					},
					success: function(result) {
						that.plateformer = Object.assign(that.plateformer, result.plateformer);
						if(result.user) {
							that.util.setStorage('ipuser', result.user);
						}
					}
				})
			}
		},
		mounted() {
			this.onLoad();
			this.util.imap();
			this.util.icloudapi();
		}
	}
</script>

<style>
	#member-mine .userinfo .avatar{
		width: 50px;
		height: 50px;
		border-radius: 50%;
	}
	#member-mine .van-icon-arrow{
		color: #999;
		font-size: 12px;
		line-height: 24px;
		margin-left: 5px;
	}
</style>


